@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-size: 19px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Roboto", sans-serif;
  line-height: 1.5;
  color: #102e4a;
  background-color: #efedef;
}

.app {
  max-width: 500px;
  padding-bottom: 30px;
  padding-top: 30px;
}

/* header section ---------------------- */

.question-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.category {
  max-width: 60%;
}

.category select {
  border-color: #dbdbdb;
  border-radius: 4px;
  color: #363636;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  width: 100%;
  padding: 5px 3px;
  cursor: pointer;
}

.category p {
  font-size: 16px;
  padding-left: 6px;
  margin: 0;
}

.score {
  display: flex;
  align-items: center;
  text-align: right;
  font-size: 16px;
  line-height: 1;
}

.score > div:first-child {
  margin-right: 25px;
}

.score .incorrect {
  color: #990621;
  font-weight: bold;
}

.score .correct {
  color: #3e5a32;
  font-weight: bold;
}

.score strong {
  color: #102e4a;
  font-size: 40px;
  display: block;
  margin-bottom: 3px;
}

.result.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.result.modal .overlay {
  position: absolute;
  background: rgba(0, 0, 0, 0.5);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}

.result .modal-content {
  background: #fff;
  border-radius: 5px;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
  padding: 30px;
  width: 400px;
  margin: 0 auto;
  max-width: 90%;
  text-align: center;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.result.modal.is-correct span {
  color: #3e5a32;
}

.result.modal.is-incorrect span {
  color: #990621;
}

.result .modal-content span {
  font-size: 30px;
  line-height: 1.5;
  margin-bottom: 20px;
}

.result .modal-content .correct-answer {
  margin-bottom: 20px;
}

.result .modal-content .correct-answer small {
  color: #777;
}

.result .modal-content .correct-answer strong {
  font-size: 40px;
}

/* main section ---------------------- */

.question-main {
  background: #ffffff;
  border-radius: 10px;
  padding: 2.5rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  min-width: 500px;
  max-width: 90%;
  min-height: 350px;
  display: flex;
  align-items: center;
}

.question {
  width: 100%;
}

.question h2 {
  margin-top: 0;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
}

button {
  display: block;
  width: 100%;
  margin-bottom: 8px;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  cursor: pointer;
  color: #102e4a;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  padding: 0.8rem;
  border-width: 1px;
  border-radius: 0.25rem;
  border-color: #3379b0;
  background-color: #66cdff;
  transition: 0.2s ease all;
  line-height: 1;
}

button:hover {
  background-color: #f15bb5;
  border-color: #9a2e6a;
  box-shadow: 2px 5px 4px 0 rgba(155, 93, 229, 0.1);
  color: #ffffff;
}

/* footer section ---------------------- */

.question-footer {
  margin-top: 15px;
  text-align: right;
}

.question-footer button {
  background: #242038;
  border-color: #08090a;
  color: #fffeff;
  width: auto;
  margin-left: auto;
  font-size: 14px;
  padding: 10px 20px;
}

